.light-theme{

   --bgvar:white;
  --bgcolvar:#121315;
  --bgbackvar:rgba(255, 255, 255, 0.771);
  
}
.dark-theme{
   --bgvar:#282c34;
  --bgcolvar: rgba(255, 255, 255, 0.821);
  --bgbackvar:#14141bc3;
}
body{
  background-color: var(--bgvar);
}


.backAndTheme{
  display: flex;
  width: 50rem;
  max-width: 95%;
  margin: 4rem auto;
  padding: 0px 15px;
}
.back-btn-icon{
  color: rgb(19, 0, 0);
  font-size: 25px;
  font-weight: bold;
  
}
.page-container {
  position: relative;
  min-height: 82vh;
}
.content-wrap {
  padding-bottom: 2.5rem;    /* Footer height */
}



