.ExpenseDateDay{
    font-size: 16px;
    font-weight: bold;
}
.date{
    display: flex;
    background-color: var(--bgbackvar);
    margin: 10px;
    padding: 12px;
    color: var(--bgcolvar);
    flex-direction: column;
    padding: 5px 25px;
    border: 1px solid var(--bgcolvar);  
    font-weight: bold;
   font-size: 16px;
}

@media (max-width:500px)
{
    .ExpenseDateDay{
      font-size: 13px;
    }
    .date{
      font-size: 14px;
      margin:5px;
      padding: 6px;
      
    }
    
}