.expense-form{
    display: flex;
    text-align: left;
    flex-wrap: wrap;
    color: var(--bgcolvar);
    background-color: var(--bgbackvar);
   
    
}
.expense-form-input{
    display: flex;
    flex-direction: column;
    width:45% ;
    margin: 8px ;

}
.expense-form-label{
    font-family:inherit;
    font-weight: bold;
    margin: 5px;
    color: var(--bgcolvar);
}
.expense-form-button{
    text-align: right;     
}
.btn{
    cursor: pointer;
    font-size: 35px;
    color:#282c34;
    padding: 0px !important;
}
.input-field{
    height:35px;
    font-family: inherit;
    font-size: 17px; 
    font-weight: bold;   
    border-radius: 6px;
    outline: none;
    border: 0.5px solid var(--bgcolvar);
   
}
.required{
font-family: inherit;
color:rgb(255, 0, 0);
}

input:focus{
   background-color: rgb(235, 221, 248);
}

@media (max-width:500px)
{
    .expense-form{
        display: flex;       
        color: var(--bgcolvar);
    }
    .expense-form-input{
        display: flex;
        flex-direction: column;
        width:85% ;
        margin: 8px ;
        font-size: 15px;
    }
    .btn{
        font-size: 26px;        
    }
    .input-field{
        height:32px;
        font-size: 15px;
       
    }
}
