.total-item-expense__discription{
    display: flex ;
    flex-direction: row;
    gap:1rem;
    /* align-items: flex-end; */
    flex-flow: column-reverse;
    justify-content: flex-start;
    margin: 0px 30px;
    flex: 1;
    color: rgb(241, 12, 12);
    font-size: 21px;
    
}
.total-item-income__discription{
    display: flex ;
    flex-direction: row;
    gap:1rem;
    /* align-items: flex-end; */
    flex-flow: column-reverse;
    justify-content: flex-start;
    margin: 0px 30px;
    flex: 1;
    color: rgb(0, 255, 26);
    font-size: 21px;
    
}

.total-item-expense__price{
    color: rgb(200, 4, 4);
    padding: 0.5rem;
    border: 2px solid var(--bgcolvar);
    background-color: var(--bgbackvar);
    font-size: 18px;
    padding: 4px 12px;
    font-weight: bold;
    margin-right: 32px;
}
.total-item-income__price{
    color:rgb(0, 255, 4);
    padding: 0.5rem;
    border: 2px solid var(--bgcolvar);
    background-color: var(--bgbackvar);
    font-size: 18px;
    padding: 4px 12px;
    font-weight: bold;
    margin-right: 32px;
}
.total-item-expense{
    display: flex ;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    box-shadow: 1px 1px 1px rgb(218, 229, 5);
    background-color: var(--bgbackvar);
    border: 0.5px solid var(--bgcolvar);
    margin: 15px 5px;
    padding: 10px ;
    width: 99%;
    
}
.total-item-income{
    display: flex ;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    box-shadow: 2px 1px 2px rgb(149, 166, 37);
    background-color: var(--bgbackvar);
    border: 2px solid var(--bgcolvar);
    margin: 15px 5px;
    padding: 10px ;
    width: 99%;
    
}

@media (max-width:500px)
{
    .total-item-expense,.total-item-income{
       padding:6px 6px 4px 8px;
       margin-bottom:2rem ;
       justify-content: left;
       flex-direction: row;

        
    }
    
    .total-item-expense__price,.total-item-income__price{
    
        padding: 0.1rem;
        font-size: 15px;
        padding: 2px 6px;
        font-weight: bold;
        margin-right: 7px;
        border-radius: 8px;
    }
   .total-item-expense__discription,.total-item-income__discription{
        display: flex ;
        flex-direction: row;
        
        /* align-items: flex-end; */
        flex-flow: column-reverse;
        justify-content: flex-start;
        margin:3px 15px 2px 0px;
        font-size: 16px;
        
    }
}